<template>
  <div class="invite">
    <div class="title-info">
      <span>
        <span class="title-bold"> 1. </span>分享本页面或发送邀请码给好友</span>
      <br><br>
      <span>
        <span class="title-bold"> 2. </span> 被邀请的好友下载打开“我是谜”，注册并填写您的邀请码。然后完成一局游戏（包括新手本），视为邀请成功。</span>
      <br><br>
      <span>
        <span class="title-bold"> 3. </span> 邀请成功后，邀请人和被邀请人都将获得奖励。</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "invite",
  data() {
    return {};
  },
  created() {}
};
</script>

<style lang="less" scoped>
.invite {
  height: 13.555rem;
  background: url(../assets/images/bg.png) no-repeat left top; /* 750 2711 */
  background-size: 100%;
  position: relative;
  .title-info {
    position: absolute;
    top: 3.5rem;
    left: 0.5rem;
    max-width: 2.7rem;
    font-size: 0.14rem;
    color: #000;
    font-weight: 600;
    span {
      .title-bold {
        font-size: 0.16rem;
        font-weight: bold;
        background: url(../assets/images/juxing@2x.png) no-repeat left top; /* 750 2711 */
        background-size: contain;
      }
    }
  }
}
</style>






