export default (function () {
  window.onload = () => {
    let design = 375,
      base = 100,
      minWidth = 375,
      maxWidth = 720,
      timer = null,
      refreshWindow = () => {
        let wWidth = document.documentElement.clientWidth
        wWidth < minWidth && (wWidth = minWidth)
        wWidth > maxWidth && (wWidth = maxWidth)
        let currentFontSize = wWidth * base / design
        document.documentElement.style.fontSize = currentFontSize + 'px'
      }
    refreshWindow()
    window.addEventListener('resize', () => {
      clearTimeout(timer)
      timer = setTimeout(refreshWindow, 10)
    }, false)
  }
})()



